https://velhobit.com.br/design/como-fazer-efeito-float-label-animado-com-css3-puro.html */

.label-float {
  position: relative;

}

.label-float input {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: white;
  border: 2px solid lightgrey;
  border-radius: 5px;
  font-size: 16px;
  min-width: 250px;
  outline: none;
  padding: 5px 20px 3px;
  transition: all 0.1s linear;
}

.label-float input:focus {
  border: 2px solid #3951b2;
}

.label-float input::-webkit-input-placeholder {
  color: transparent;
}

.label-float input:-ms-input-placeholder {
  color: transparent;
}

.label-float input::placeholder {
  color: transparent;
}

.label-float p {
  background-color: white;
  box-sizing: border-box;
  left: 15px;
  padding: 0 5px;
  pointer-events: none;
  position: absolute;
  top: 0;
  transform: translateY(32%);
  transition: all 0.1s linear;
}

.label-float input:not( :-ms-input-placeholder ) + p {
  color: #3951b2;
  font-size: 13px;
  top: 0;
  transform: translateY(-50%);
}

.label-float input:not( :placeholder-shown ) + p {
  color: #3951b2;
  font-size: 13px;
  top: 0;
  transform: translateY(-50%);
}

.label-float input:required:invalid + p {
  color: red;
}

.label-float input:focus:required:invalid {
  border: 2px solid red;
}

.label-float input:required:invalid + p:before {
  content: '*';
}

